"use server";

interface GetAudioUrlInput {
  userUuid: string | null;
  slideNumber: number;
  voiceId: string;
}

interface GetAudioUrlResult {
  url: string;
}

const S3_BUCKET_ID = "da2ac12a-41cc63a3-3ad9-4eee-a33a-7776f7973de7";
const S3_BASE_URL = "https://s3.twcstorage.ru";

/**
 * Получает URL аудио файла из S3 хранилища
 * @param userUuid - UUID залогиненного пользователя
 * @param slideNumber - Номер слайда
 * @param voiceId - ID голоса из таблицы voices
 * @returns URL аудио файла
 */
export async function GetAudioUrl({
  userUuid,
  slideNumber,
  voiceId,
}: GetAudioUrlInput): Promise<GetAudioUrlResult> {
  if (!userUuid) {
    throw new Error("UUID пользователя не указан");
  }

  if (!voiceId) {
    throw new Error("voice_id не указан");
  }

  const audioUrl = `${S3_BASE_URL}/${S3_BUCKET_ID}/temp/${userUuid}/TTS/${slideNumber}_${voiceId}`;

  return { url: audioUrl };
}

